$('.product-card__favorite').on('click', function(){
  $(this).toggleClass('active');
})

//Формат
const money = new Intl.NumberFormat('ru-RU', {
  style: 'currency',
  currency: 'RUB',
  maximumFractionDigits: 0
});

$('.product-card__price-current').each(function(){
  let number = $(this).text();
  $(this).text(money.format(number));
});

let disc = $('.product-card__price-old') ?? false;
if(disc) {
  disc.each(function(){
    let number = $(this).text();
    $(this).text(money.format(number));
  });
}

let insta = $('.product-card__installments') ?? false;
if(insta) {
  insta.each(function(){
    let number = $(this).text();
    $(this).text(money.format(number));
  });
}
/* Button preventDefault */
$('.product-card__buy').on('click',function(e){
  e.preventDefault();
});
$('.product-card__bonus').on('click',function(e){
  e.preventDefault();
})
