export const modalClose = () => {
  //Отмена по кнопке "Закрыть" и по фону
  $('.modal__button, .modal__close').on('click', function () {
    modalCloseFunc();
  });

  $('.modal-overlay').on('click', function () {
    modalCloseFunc();
  });

  //Отмена по Esc
  $(document).on('keydown', function (e) {
    const keyCode = e.keyCode || e.which;
    if (keyCode === 27) {
      modalCloseFunc();
    }
  });
};

//Закрытие модалки
export const modalCloseFunc = () => {
    $('body, .modal').css('padding-right', 0);
    $('.modal, .modal-overlay, html').removeClass('modal-showed');
    $('.modal-form__hidden, .modal-form__visible').removeClass('form-done');
};

//открытие модального окна
export const modalOpen = (el) => {
  let modalName = $(el).data('modal'),
    body = $('body').width();
  let scrollWidth = window.innerWidth - body;


  //Показ модалки
  $(`.modal[data-modal="${modalName}"]`).addClass('modal-showed');

  //Фиксируем страницу
  $('html').addClass('modal-showed');
  if (scrollWidth > 0) {
    // не забыть убрать .header
    $('body, .modal,').css('padding-right', scrollWidth);
  }
};
